.events {
    margin: 20px 100px;
}

.events-h4 {
    margin: 20px 0 10px 0;
}

ul {
    border: 1px solid #ddd;
}

ul:first-child, ul:last-child {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}

li {
    list-style: none;
    padding: 10px 15px;
    border-radius: 4px;
}